import { render } from '@testing-library/preact';
import { expect } from 'chai';
import { InputPassword } from '../';
import type { RootEnvironment } from '../../UNSAFE_Environment';
import { RootEnvironmentProvider } from '../../UNSAFE_Environment';
import { matchTranslationBundle } from '../../utils/UNSAFE_matchTranslationBundle';

async function getEnvironmentContext() {
  const locale = matchTranslationBundle(['en'], new Set(['en-US', 'en']));
  const { default: translations } = await import(`../../resources/nls/${locale}/bundle.ts`);
  return {
    translations: { '@oracle/oraclejet-preact': translations }
  } as Partial<RootEnvironment>;
}

const noOp = () => {};

describe('Test InputPassword component', () => {
  describe('Test InputPassword translation bundle', () => {
    it('render', async () => {
      const env = await getEnvironmentContext();
      const content = render(
        <RootEnvironmentProvider environment={env}>
          <InputPassword label="Test Label" onInput={noOp} />
        </RootEnvironmentProvider>
      );
      const revealButton = content.container.querySelector('button') as HTMLButtonElement;
      expect(revealButton.getAttribute('aria-label')).equals('Password Hidden');
    });
  });
});
